/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";
/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import "~@ionic/angular/css/display.css";
@import "~flag-icons/css/flag-icons.min.css";

/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";

@import "~swiper/scss";
@import "~swiper/scss/pagination";

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  src: url('./assets/fonts/Montserrat/Montserrat-Regular.ttf');
  src: url('./assets/fonts/Montserrat/Montserrat-SemiBold.ttf');
}

:root {
  --ion-font-family: "Montserrat";
  --primary-color: #456efe;
}

.app-font {
  font-family: "Montserrat" !important;
}

// Form fields override start
// Later move it to standalone css
ion-toolbar {
  --min-height: 75px;
}

ion-item {
  --highlight-color-valid: #456efe !important;
}

ion-item::part(native) {
  border-radius: 16px;
}

ion-popover::part(content) {
  border-radius: 10px !important;
  border-bottom-left-radius: 30px !important;
  border-bottom-right-radius: 30px !important;
}

// Form fields override end

.swiper-pagination-bullets {
  position: unset !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

// .alertCancel .alert-wrapper {
//   // background: #8e2323;
//   width: 70% !important;
//   border-radius: 35px;
// }

ion-modal.internet-modal {
  --height: 35%;
  --width: 340px;
  --border-radius: 16px;
  --box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  backdrop-filter: blur(10px);
}

// Alert SCSS START

ion-alert.logout-alert .alert-wrapper {
  --min-width: 330px !important;
  --width: 330px !important;
  border-radius: 20px !important;
}

ion-alert.logout-alert {
  backdrop-filter: blur(10px);
}

ion-alert.logout-alert .alert-button-group {
  justify-content: center;
}

ion-alert.logout-alert .alert-button {
  font-weight: 100 !important;
  text-transform: capitalize !important;
}

button.alert-button.alert-button-confirm {
  background-color: #C90303;
  color: #FFFFFF;
  width: 45%;
  border-radius: 50px;
  height: 50px;
}

.logout-alert .alert-button-inner {
  justify-content: center !important;
}

button.alert-button.alert-button-cancel {
  background-color: #456EFE;
  color: #FFFFFF;
  width: 45%;
  border-radius: 50px;
  height: 50px;
}

.full-width {
  width: 100%;
}

.logo-icon {
  margin: 0 auto !important;
  text-align: start !important;
  width: 230px !important;
  height: 45px !important;
  margin-top: 60px !important;
  opacity: 100% !important;
}

// Alert SCSS END

.small-modal {
  &::part(content) {
    --width: 90%;
    --height: 50%;
    --border-radius: 16px;
  }
}

// .small-modal::part(backdrop) {
//   background: rgba(209, 213, 219);
//   opacity: 1;
// }

.t-5 {
  margin-top: 5px;
}

.t-10 {
  margin-top: 10px;
}

.t-30 {
  margin-top: 30px;
}

// Modal SCSS

ion-modal#example-modal {
  --width: fit-content;
  --min-width: 250px;
  --height: fit-content;
  --border-radius: 6px;
  --box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
  padding: 16px;
}

ion-modal#example-modal h1 {
  margin: 20px 20px 10px 20px;
}

ion-modal#example-modal ion-icon {
  margin-right: 6px;
  padding: 4px 0;
  font-size: 32px;
  color: #aaaaaa;
}

ion-modal#example-modal .wrapper {
  margin-bottom: 10px;
}

.custom_action_css button {
  --color: #000 !important;
  font-weight: 700;
  font-family: montserrat !important;
  font-size: 16px !important;
}


.action-sheet-container.sc-ion-action-sheet-ios {
  padding-left: unset;
  padding-right: unset;
  -webkit-padding-start: 18px;
  padding-inline-start: 18px;
  -webkit-padding-end: 18px;
  padding-inline-end: 18px;
}

.action-sheet-button.sc-ion-action-sheet-ios {
  padding-left: unset;
  padding-right: unset;
  -webkit-padding-start: 18px;
  padding-inline-start: 18px;
  -webkit-padding-end: 18px;
  padding-inline-end: 18px;
  color: black;
  background-color: white;
  font-size: 16pt;
  font-weight: bold;
  font-family: "Montserrat" !important;
  border-bottom: 1px solid #ccc;
}

.alert-title.sc-ion-alert-md {
  padding-left: 75px;
  font-weight: 700;
}

.alert-sub-title.sc-ion-alert-md {
  padding-left: 16px;
  font-weight: 600;
  padding-top: 7px;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  //display: none;
  //-webkit-appearance: none;
  width: 15px;
  padding: 0px;
  margin: 0px;
  margin-top: 10px;
}